import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import BtnEdit from 'components/common/button/BtnEdit';
import BtnDelete from 'components/common/button/BtnDelete';
import { Card, Row, Table, Popconfirm, Col } from 'antd';
import { unitKnowledge } from 'dump/data';

const columns = [
  {
    title: 'Tên kiến thức',
    dataIndex: 'knowledgeName',
    key: 'knowledgeName',
  },
  {
    title: 'Thao tác',
    dataIndex: 'operation',
    render: (text, record) =>
      unitKnowledge.length >= 1 ? (
        <Fragment>
          <BtnEdit href={`#unit-knowledge/form/edit/${record.code}`} />
          <Popconfirm title="Sure to delete?" onConfirm={() => this.handleDelete(record.key)}>
            <BtnDelete />
          </Popconfirm>
        </Fragment>
      ) : null,
  },
];

class UnitKnowledgePage extends Component {
  state = {};

  render() {
    return (
      <Card title="Danh mục khối kiến thức">
        <Row>
          <Col span={24}>
            <BtnAddNew href="#/unit-knowledge/form/new" />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table bordered dataSource={unitKnowledge} columns={columns} />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <UnitKnowledgePage />
  </Page>
);



